.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(4, 4, 4, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.3s linear, visibility 0.3s linear;
}
.modal {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  border: 1px solid rgba(239, 237, 232, 0.2);
  background-color: #10100f;
  padding: 48px 24px;
  width: 100%;
  max-width: 335px;
  height: 280px;
  color: #efede8;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}
.modalCloseBtn {
  position: absolute;
  top: 14px;
  right: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}
.svgclose {
  width: 28px;
  height: 28px;
}
